*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Courier New', Courier, monospace;
  /* background: #ecf0f1; */
}

.header{
  width: 100%;
  line-height: 80px;
  /* background-color: #f5ba13; */
  background-color: #b7ff00;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  color: rgb(212, 212, 208);
  box-shadow: 5px 3px 15px -5px rgba(0, 0, 0, 0.3);
}

.header h1{
  color: rgb(255, 255, 255);
}

.main_note{
  width: 500px;
  height: 300px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
    /* width: 500px;
    height: 85px;
    padding: 11px 11px 11px 11px;
    margin: 20px auto; */
}

.my_notes h1{
  color: #34495e;
}

.my_notes p{
  color: gray;
}

form{
  min-height: 52px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgb(0 0 0 / 30%);
  border-radius: 10px;
  /* border-color: #080808; */
  border: 2px solid #1b181826;
  position: relative;
}

input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}


textarea{
  width: 100%;
  line-height: 11px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
}

.MuiButton-root{
  width: 30px;
  height: 50px;
  position: absolute;
  bottom: -28px;
  left: 400px;
  cursor: pointer;
  background-color: #fff !important ;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  border-radius: 50% !important;
}

.MuiButton-root .plus-sign{
  font-size: 45px !important;
  font-weight: 900;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}

.MuiButton-root:hover{
  background: #f4b400 !important;
}

.MuiButton-root:hover .plus-sign{
  color: #fff;
  background: transparent;
}

.note{
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}

.note h1{
  font-size: 1.7rem;
  margin: 6px;
}

.note p{
  font-size: 1.1rem;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note .btn{
  position: relative;
  float: right;
  margin-right: 10px;
  /* color: #f5ba13; */
  color: #f4b400;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  border-bottom: 3px 3px 15px -10px rgba(0, 0, 0, 0.3);
  border-radius: 50%;
}

.deleteIcon{
  width: 0.7rem;
  height: 0.7rem;
}

.note .btn:hover{
  background-color: #f5ba13;
}

.note .btn:hover .deleteIcon{
  color: #fff;
}

footer{
  position: absolute;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 1.5rem;
}
